.Clock {
	display: inline-block;
	color: #808080;
	vertical-align: top;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
	user-select: none;
}

@media only screen and (max-device-width: 480px) {
	.Clock {
		display: none;
	}
}

@media only screen and (device-width : 375px) and (device-height : 812px) and (-webkit-device-pixel-ratio : 3) {

	.Clock {
		display: none;
	}
}